.tweetCard{
    position: relative;
    color: #FFFFFF;
    background-color: rgb(146, 138, 138);
    width: 600px;
    height: 100px;
    border:2px black solid ;
    margin: .4em;
    background-color:#343A40;
}

.tweetId{
    position: absolute;
    left: 20px;
    top: 20px;
    margin: 0px;

}

.tweetTime{
    position: absolute;
    top:18px;
    right:20px;
    margin: 0px;
}

.tweetText{
    position: absolute;
    width: 455px;
    top: 48px;
    left: 20px;
    overflow: auto;
    margin: 0px;
}
.tweet-list{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: auto;
}